/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 15px;
  font-family: "League Spartan", sans-serif;
  color: hsl(300deg, 43%, 22%);
}

body {
  background-color: hsl(0deg, 0%, 100%);
  display: flex;
  justify-content: center;
  align-items: center;
}

main {
  width: calc(100% - 50px);
  margin: 12vh 0;
}
main .sections-wrapper {
  display: flex;
  flex-direction: column;
  text-align: center;
}
main .sections-wrapper .statement {
  margin-bottom: 2.5rem;
}
main .sections-wrapper .statement h1 {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 2.2rem;
  margin-bottom: 1.4rem;
}
main .sections-wrapper .statement p {
  font-size: 1.1rem;
  font-weight: 500;
  line-height: 1.6rem;
  color: hsl(303deg, 10%, 53%);
}
main .sections-wrapper .ratings-container {
  margin-bottom: 2.5rem;
}
main .sections-wrapper .ratings-container .rating-wrapper {
  background-color: hsl(300deg, 24%, 96%);
  padding: 1rem 0;
  border-radius: 8px;
  margin-bottom: 1rem;
}
main .sections-wrapper .ratings-container .rating-wrapper .stars {
  margin-bottom: 1rem;
}
main .sections-wrapper .ratings-container .rating-wrapper h2 {
  font-size: 1.1rem;
  font-weight: 700;
}
main .reviews-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
}
main .reviews-container .review-card {
  background-color: hsl(300deg, 43%, 22%);
  border-radius: 8px;
  padding: 2rem;
  color: hsl(300deg, 24%, 96%);
}
main .reviews-container .review-card .profile-wrapper {
  display: flex;
  align-items: center;
  margin: 0.5rem 0 1.5rem;
}
main .reviews-container .review-card .profile-wrapper img {
  border-radius: 50%;
  margin-right: 1.5rem;
  width: 40px;
}
main .reviews-container .review-card .profile-wrapper .profile-detail h3 {
  font-weight: 500;
  margin-bottom: 0.4rem;
}
main .reviews-container .review-card .profile-wrapper .profile-detail .status {
  font-weight: 500;
  color: hsl(333deg, 80%, 67%);
  letter-spacing: 0.05rem;
}
main .reviews-container .review-card .review {
  font-size: 1.1rem;
  line-height: 1.5rem;
}

@media screen and (min-width: 768px) {
  body {
    height: 100vh;
  }
  main {
    max-width: 1110px;
    margin: 0;
  }
  main .sections-wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4.8rem;
  }
  main .sections-wrapper .statement {
    width: 450px;
    margin-bottom: 0;
    text-align: left;
  }
  main .sections-wrapper .statement h1 {
    font-size: 3.8rem;
    line-height: 3.2rem;
    letter-spacing: -0.1rem;
    margin-bottom: 1.8rem;
  }
  main .sections-wrapper .statement p {
    font-size: 1.2rem;
  }
  main .sections-wrapper .ratings-container {
    margin-bottom: 0;
  }
  main .sections-wrapper .ratings-container .rating-wrapper {
    padding: 1rem 1.8rem;
  }
  main .sections-wrapper .ratings-container .rating-wrapper:nth-child(1) {
    transform: translateX(-80px);
  }
  main .sections-wrapper .ratings-container .rating-wrapper:nth-child(2) {
    transform: translateX(-40px);
  }
  main .reviews-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
  main .reviews-container .review-card {
    padding: 2rem 2rem 2.5rem;
  }
  main .reviews-container .review-card .profile-wrapper {
    margin: 0.7rem 0 1.7rem;
  }
  main .reviews-container .review-card:nth-child(2) {
    transform: translateY(16px);
  }
  main .reviews-container .review-card:nth-child(3) {
    transform: translateY(32px);
  }
}
@media screen and (min-width: 950px) {
  .rating-wrapper {
    display: flex;
    align-items: center;
    padding: 1.2rem 2.5rem;
  }
  .rating-wrapper .stars {
    margin-right: 2rem;
    margin-bottom: 0 !important;
  }
  .rating-wrapper .stars svg {
    margin-left: 0.3rem;
  }
}